import React from "react"
import { Link } from "gatsby"

const Menu = () => {
    return (
        <ul className="page-list">
            <li className="item"><Link to="/">BLOG</Link></li>
            {/* <li className="item"><Link to="/cine">CIN</Link></li> */}
            <li className="item"><Link to="/a-propos/"> PROPOS</Link></li>
        </ul>
    )
}

export default Menu